import React, { useState, useEffect } from 'react';
import { supabase } from '../services/supabase';
import { Plus, Trash2, Edit2, LogOut, Image as ImageIcon, Save, X } from 'lucide-react';
import { useNavigate } from 'react-router-dom';

export default function AdminDashboard() {
    const [products, setProducts] = useState([]);
    const [loading, setLoading] = useState(true);
    const [editing, setEditing] = useState(null); // Product being edited (null = mode list)
    const navigate = useNavigate();

    // Form State
    const [formData, setFormData] = useState({
        name: '',
        price: '',
        category: '',
        description: '',
        image_url: '',
        images: [],
        is_new: false
    });

    const [uploading, setUploading] = useState(false);

    const handleImageUpload = async (e) => {
        try {
            setUploading(true);
            const files = Array.from(e.target.files);
            if (files.length === 0) return;

            const newImageUrls = [];

            for (const file of files) {
                const fileExt = file.name.split('.').pop();
                const fileName = `${Math.random()}.${fileExt}`;
                const filePath = `${fileName}`;

                const { error: uploadError } = await supabase.storage
                    .from('product-images')
                    .upload(filePath, file);

                if (uploadError) throw uploadError;

                const { data } = supabase.storage
                    .from('product-images')
                    .getPublicUrl(filePath);

                newImageUrls.push(data.publicUrl);
            }

            setFormData(prev => {
                const updatedImages = [...(prev.images || []), ...newImageUrls];
                return {
                    ...prev,
                    images: updatedImages,
                    image_url: updatedImages[0] // Set primary image
                };
            });
        } catch (error) {
            alert('Erro ao fazer upload: ' + error.message);
        } finally {
            setUploading(false);
        }
    };

    useEffect(() => {
        fetchProducts();
    }, []);

    const fetchProducts = async () => {
        try {
            const { data, error } = await supabase
                .from('products')
                .select('*')
                .order('created_at', { ascending: false });

            if (error) throw error;
            setProducts(data);
        } catch (error) {
            console.error('Error fetching products:', error);
        } finally {
            setLoading(false);
        }
    };

    const handleLogout = async () => {
        await supabase.auth.signOut();
        navigate('/admin');
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);

        try {
            if (editing?.id) {
                // Update
                const { error } = await supabase
                    .from('products')
                    .update(formData)
                    .eq('id', editing.id);
                if (error) throw error;
            } else {
                // Create
                const { error } = await supabase
                    .from('products')
                    .insert([formData]);
                if (error) throw error;
            }

            await fetchProducts();
            setEditing(null);
            resetForm();
        } catch (error) {
            alert('Erro ao salvar: ' + error.message);
        } finally {
            setLoading(false);
        }
    };

    const handleDelete = async (id) => {
        if (!window.confirm('Tem certeza que deseja apagar?')) return;
        try {
            const { error } = await supabase.from('products').delete().eq('id', id);
            if (error) throw error;
            fetchProducts();
        } catch (error) {
            alert('Erro ao apagar: ' + error.message);
        }
    };

    const handleEdit = (product) => {
        setEditing(product);
        setFormData(product);
    };

    const resetForm = () => {
        setFormData({
            name: '',
            price: '',
            category: '',
            description: '',
            image_url: '',
            images: [],
            is_new: false
        });
    };

    return (
        <div className="min-h-screen bg-stone-50 pb-20">
            {/* Header */}
            <header className="bg-white border-b border-stone-200 px-6 py-4 flex justify-between items-center sticky top-0 z-20">
                <div>
                    <h1 className="text-xl font-serif font-bold text-stone-800">Painel Zenaide</h1>
                    <p className="text-xs text-stone-500">Gerenciamento de Produtos</p>
                </div>
                <button onClick={handleLogout} className="p-2 text-stone-400 hover:text-red-500 transition-colors">
                    <LogOut size={20} />
                </button>
            </header>

            <div className="max-w-4xl mx-auto p-6">

                {/* Toggle Create Mode */}
                {!editing && (
                    <button
                        onClick={() => { setEditing({}); resetForm(); }}
                        className="w-full bg-stone-900 text-white py-4 rounded-xl font-bold flex items-center justify-center gap-2 mb-8 shadow-lg hover:bg-stone-800 transition-all"
                    >
                        <Plus size={20} />
                        Adicionar Novo Produto
                    </button>
                )}

                {/* EDITOR FORM */}
                {editing && (
                    <div className="bg-white rounded-2xl shadow-xl border border-stone-100 p-6 mb-8 animate-in slide-in-from-top-4">
                        <div className="flex justify-between items-center mb-6">
                            <h2 className="text-lg font-bold text-stone-800">
                                {editing.id ? 'Editar Produto' : 'Novo Produto'}
                            </h2>
                            <button onClick={() => { setEditing(null); resetForm(); }} className="p-2 text-stone-400 hover:text-stone-600">
                                <X size={20} />
                            </button>
                        </div>

                        <form onSubmit={handleSubmit} className="space-y-4">
                            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                                <div>
                                    <label className="block text-xs font-bold text-stone-500 uppercase mb-1">Nome do Produto</label>
                                    <input
                                        required
                                        value={formData.name}
                                        onChange={e => setFormData({ ...formData, name: e.target.value })}
                                        className="w-full px-4 py-2 bg-stone-50 border border-stone-200 rounded-lg focus:outline-none focus:border-amber-500"
                                    />
                                </div>
                                <div>
                                    <label className="block text-xs font-bold text-stone-500 uppercase mb-1">Categoria</label>
                                    <select
                                        required
                                        value={formData.category}
                                        onChange={e => setFormData({ ...formData, category: e.target.value })}
                                        className="w-full px-4 py-2 bg-stone-50 border border-stone-200 rounded-lg focus:outline-none focus:border-amber-500"
                                    >
                                        <option value="">Selecione...</option>
                                        <option value="Novidades">Novidades</option>
                                        <option value="Rommanel">Rommanel</option>
                                        <option value="Casa">Casa</option>
                                        <option value="Presentes">Presentes</option>
                                        <option value="Promo">Promo</option>
                                        <option value="Semijoias">Semijoias</option>
                                    </select>
                                </div>
                            </div>

                            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                                <div>
                                    <label className="block text-xs font-bold text-stone-500 uppercase mb-1">Preo (R$)</label>
                                    <input
                                        type="number"
                                        step="0.01"
                                        required
                                        value={formData.price}
                                        onChange={e => setFormData({ ...formData, price: e.target.value })}
                                        className="w-full px-4 py-2 bg-stone-50 border border-stone-200 rounded-lg focus:outline-none focus:border-amber-500"
                                    />
                                </div>
                                <div className="flex items-center gap-3 pt-6">
                                    <input
                                        type="checkbox"
                                        checked={formData.is_new}
                                        onChange={e => setFormData({ ...formData, is_new: e.target.checked })}
                                        className="w-5 h-5 accent-amber-500"
                                    />
                                    <label className="text-sm font-medium text-stone-700">Marcar como "Novo"?</label>
                                </div>
                            </div>

                            <div>
                                <label className="block text-xs font-bold text-stone-500 uppercase mb-1">Fotos do Produto</label>
                                <div className="space-y-3">
                                    <div className="relative">
                                        <div className="flex items-center justify-center w-full">
                                            <label className={`flex flex-col items-center justify-center w-full h-32 border-2 border-stone-300 border-dashed rounded-lg cursor-pointer bg-stone-50 hover:bg-stone-100 ${uploading ? 'opacity-50 cursor-not-allowed' : ''}`}>
                                                <div className="flex flex-col items-center justify-center pt-5 pb-6">
                                                    <ImageIcon className="w-8 h-8 mb-4 text-stone-500" />
                                                    <p className="mb-2 text-sm text-stone-500"><span className="font-semibold">Clique para fazer upload</span></p>
                                                    <p className="text-xs text-stone-500">Pode selecionar vrias fotos</p>
                                                </div>
                                                <input
                                                    type="file"
                                                    className="hidden"
                                                    multiple
                                                    accept="image/*"
                                                    onChange={handleImageUpload}
                                                    disabled={uploading}
                                                />
                                            </label>
                                        </div>
                                    </div>

                                    {/* Preview Grid */}
                                    {formData.images && formData.images.length > 0 && (
                                        <div className="grid grid-cols-4 gap-2">
                                            {formData.images.map((img, idx) => (
                                                <div key={idx} className="relative group aspect-square">
                                                    <img src={img} alt={`Preview ${idx}`} className="w-full h-full object-cover rounded-lg border border-stone-200" />
                                                    <button
                                                        type="button"
                                                        onClick={() => {
                                                            const newImages = formData.images.filter((_, i) => i !== idx);
                                                            setFormData(prev => ({
                                                                ...prev,
                                                                images: newImages,
                                                                image_url: newImages.length > 0 ? newImages[0] : ''
                                                            }));
                                                        }}
                                                        className="absolute top-1 right-1 p-1 bg-red-500 text-white rounded-full opacity-0 group-hover:opacity-100 transition-opacity"
                                                    >
                                                        <X size={12} />
                                                    </button>
                                                    {idx === 0 && (
                                                        <span className="absolute bottom-1 left-1 bg-amber-500 text-white text-[10px] px-1.5 rounded font-bold">Capa</span>
                                                    )}
                                                </div>
                                            ))}
                                        </div>
                                    )}
                                </div>
                            </div>

                            <div>
                                <label className="block text-xs font-bold text-stone-500 uppercase mb-1">Descrio</label>
                                <textarea
                                    rows="3"
                                    value={formData.description}
                                    onChange={e => setFormData({ ...formData, description: e.target.value })}
                                    className="w-full px-4 py-2 bg-stone-50 border border-stone-200 rounded-lg focus:outline-none focus:border-amber-500"
                                    placeholder="Detalhes opcionais..."
                                />
                            </div>

                            <div className="flex gap-3 pt-2">
                                <button
                                    type="submit"
                                    disabled={loading || uploading}
                                    className="flex-1 bg-amber-500 text-white font-bold py-3 rounded-xl hover:bg-amber-600 transition-colors flex items-center justify-center gap-2 disabled:opacity-50"
                                >
                                    <Save size={18} />
                                    Salvar Produto
                                </button>
                            </div>
                        </form>
                    </div>
                )}

                {/* LIST */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {products.map(product => (
                        <div key={product.id} className="bg-white p-4 rounded-xl border border-stone-100 flex gap-4 items-center">
                            <img src={product.image_url} alt={product.name} className="w-16 h-16 rounded-lg object-cover bg-stone-100" />
                            <div className="flex-1">
                                <h3 className="font-bold text-stone-800 line-clamp-1">{product.name}</h3>
                                <p className="text-xs text-stone-500">{product.category}</p>
                                <p className="text-sm font-bold text-stone-900 mt-1">R$ {product.price}</p>
                            </div>
                            <div className="flex flex-col gap-2">
                                <button onClick={() => handleEdit(product)} className="p-2 bg-stone-50 text-stone-600 rounded-lg hover:bg-amber-50 hover:text-amber-600">
                                    <Edit2 size={16} />
                                </button>
                                <button onClick={() => handleDelete(product.id)} className="p-2 bg-stone-50 text-stone-600 rounded-lg hover:bg-red-50 hover:text-red-500">
                                    <Trash2 size={16} />
                                </button>
                            </div>
                        </div>
                    ))}
                </div>

                {products.length === 0 && !loading && (
                    <div className="text-center py-10 text-stone-400">
                        Nenhum produto cadastrado.
                    </div>
                )}
            </div>
        </div>
    );
}
